
































































































import { Component, Vue } from "vue-property-decorator";
import { settings } from "@/data/settings";
import console from "@/util/console";

export class FormModel {
  action = "";
  sound = false;
  reminingTime = 100;
  fontSize = "normal";
}

@Component
export default class Settings extends Vue {
  model = new FormModel();

  constructor() {
    super();
    const instance = settings.getInstance();
    this.model.action = instance.actionAtEnd;
    this.model.sound = instance.soundOn;
    this.model.reminingTime = instance.timeRemaning;
    this.model.fontSize = instance.fontSize;
  }

  submit(): void {
    console.info("settings", this.model);

    const instance = settings.getInstance();
    instance.setTimeout(this.model.reminingTime);
    instance.setActionAtEnd(this.model.action);
    instance.setSoundOn(this.model.sound);
    instance.setFontSize(this.model.fontSize);
    this.$router.push("/");
    // settings.getInstance().setTimesPerMinute(this.model.get(this.TIMES_PER_MINUTE_INPUT).value);
    // settings.getInstance().setAccentBetweenTicks(this.model.get(this.ACCENT_BETWEEN_TICKS_INPUT).value);
  }
}
